// src/i18n.js

import i18n from "i18next";
import { initReactI18next } from "react-i18next";

const resources = {
  en: {
    translation: {
      comment: "Comment",
      edit: "Edit",
      delete: "Delete",
      save: "Save",
      like: "Like",
      dislike: "Dislike",
      translate: "Translate",
    },
  },
  es: {
    translation: {
      comment: "Comentario",
      edit: "Editar",
      delete: "Eliminar",
      save: "Guardar",
      like: "Me gusta",
      dislike: "No me gusta",
      translate: "Traducir",
    },
  },
  // You can add more languages here
};

i18n.use(initReactI18next).init({
  resources,
  lng: "en", // Default language
  fallbackLng: "en", // Use English if the language is not found
  interpolation: {
    escapeValue: false,
  },
});

export default i18n;
